import S from 'fluent-json-schema'
import { Page, PageSearchQuery, sClientEntity, sPageSearchQuery } from '../core'
import { ClientEntity } from '../core'
import {
  sWeb3ProviderConnection,
  Web3ProviderConnection,
} from '../web3-client/web3-client.model'
import { CHAINS_ROUTES_KEY } from './chain.const'

/**
 * Client Chain interface
 */
export interface Chain extends ChainData, ClientEntity {}

export function sChain(isPartial?: boolean) {
  return S.object()
    .id('#chain')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sChainData(isPartial))
}

export interface ChainData {
  name: string
  // The HEX chain ID
  hex: string
  blocksPerYear: number
  blocksMaxRange?: number
  tokenSymbol?: string
  color?: string

  // External chain IDentifier
  chainID: number
  RPCs?: ChainRpc[]
  isDisabled?: boolean
}

export function sChainData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('The official name of the blockchain.')
    .prop('hex', S.string())
    .description('The HEX code of the blockchain.')
    .prop('blocksPerYear', S.number())
    .description('Number of blocks mined every year.')
    .prop('blocksMaxRange', S.number())
    .description('Number of max blocks to be synced.')
    .prop('tokenSymbol', S.string())
    .description('The token gas symbol associated to the chain e.g. ETH, ARB')
    .prop('chainID', S.number())
    .description('The official IDentifier of the blockchain.')
    .prop('RPCs', S.array().items(sChainRpc()).minItems(1))
    .description('List of the RPCs that provide the blockchain.')
    .prop('color', S.string())
    .description('The color of the chain')
    .prop('isDisabled', S.boolean())
    .description('The flag to disable the chain.')
    .required(isPartial ? [] : ['name', 'hex', 'blocksPerYear', 'chainID'])
}

export interface ChainRpc extends Web3ProviderConnection {
  name: string
  isPublic?: boolean
}

export function sChainRpc() {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('The RPC official name.')
    .required()
    .prop('isPublic', S.boolean())
    .description('The visibility flag.')
    .extend(sWeb3ProviderConnection())
}

export enum ChainErrorCodes {
  notFound = 'CHAIN_NOT_FOUND',
  notDeletable = 'CHAIN_NOT_DELETABLE',
}

export type ChainFields =
  | '_id'
  | 'name'
  | 'hex'
  | 'RPCs'
  | 'chainID'
  | 'createdAt'
  | 'createdBy'
  | 'updatedAt'
  | 'updatedBy'

export const CHAIN_FIELDS = [
  '_id',
  'name',
  'hex',
  'RPCs',
  'chainID',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const CHAIN_SORT_FIELDS = ['_id', 'name']

export interface ChainsSearchQuery
  extends PageSearchQuery<'name', ChainFields> {
  name?: string
  'name:ct'?: string
  hex?: string
  RPCs?: boolean
  chainID?: number
}

export function sChainsSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('Name of the chain that must match.')
    .prop('name:ct', S.string())
    .description('Name of the chain that must be contained.')
    .prop('hex', S.string())
    .description('HEX of the chain that must match.')
    .prop('chainID', S.number())
    .description('The official IDentifier of the chain that must match.')
    .extend(sPageSearchQuery(CHAIN_FIELDS, CHAIN_SORT_FIELDS))
}

export interface ChainsClientModel {
  search: (params?: ChainsSearchQuery) => Promise<Page<Chain>>
  list: (params?: ChainsSearchQuery) => Promise<Chain[]>
  findOne: (params?: ChainsSearchQuery) => Promise<Chain | undefined>
  readOne: (params: ChainsSearchQuery) => Promise<Chain | undefined>
}

export enum ChainRoutes {
  v1Create = `v1/${CHAINS_ROUTES_KEY}`,
  v1Delete = `v1/${CHAINS_ROUTES_KEY}/:chainId`,
  v1Read = `v1/${CHAINS_ROUTES_KEY}/:chainId`,
  v1Update = `v1/${CHAINS_ROUTES_KEY}/:chainId`,
  v1Search = `v1/${CHAINS_ROUTES_KEY}`,
}
